.event {
  position: relative;
  width: 100vw;
  height: 100vh;

  padding: 15rem;
  display: flex;
  align-items: center;
  justify-content: center;

  @include media("<=phone") {
    box-sizing: border-box;
    padding: 15rem 3rem;
  }

  &__carousel {
    width: 100%;
    height: 100%;

    // position: relative;

    &__card {
      position: absolute;
      width: 30rem;
      z-index: -1;
    }

    // background-color: $color-secondary;

    &__container {
      width: 100%;
      display: grid;
      grid-gap: 3rem;
      grid-template-columns: repeat(3, 40rem);
      grid-template-rows: repeat(4, 40rem);

      @include media("<=phone") {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20rem;
        width: 100%;
      }
    }

    &__slide {
      position: relative;
      cursor: pointer;
      width: 30rem;
      & h1 {
        position: absolute;
        font-size: 2.1rem;
        top: 1rem;
        left: 3.5rem;
        user-select: none;

        @include media("<=phone") {
          width: 100%;
          left: 3rem;
          font-size: 2rem;
        }
      }

      & .event__carousel__poster {
        position: relative;
        top: 5rem;
        left: 3rem;
        aspect-ratio: 1/1;
        width: 25rem;
      }

      & p {
        position: absolute;
        bottom: 3rem;
        left: 4rem;
        text-align: left;
        font-size: 1.5rem;
        width: 25rem;
        text-wrap: wrap;
        user-select: none;

        @include media("<=phone") {
          bottom: -12rem;
          left: 3rem;
        }
      }
    }
  }

  &__side-container {
    position: fixed;
    top: 4.5rem;
    left: 0;
    text-align: center;
    z-index: 995;

    & img {
      @include media("<=phone") {
        width: 40rem;
      }
    }

    & h3 {
      position: absolute;
      top: 3rem;
      left: 50%;
      font-size: 5rem;

      @include media("<=phone") {
        top: 3rem;
        font-size: 3rem;
      }
    }
  }
}

//Popup
.popup__container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  padding: 5rem 7rem;
  width: 80vw;
  height: 90vh;
  background-color: $color-white;
  z-index: 1001;
  border-radius: 1rem;
  overflow: hidden;

  color: $color-secondary;

  @include media("<=phone") {
    padding: 5rem 1rem;
    width: 90vw;
  }

  &__close {
    position: absolute;
    right: 3rem;
    top: 1rem;
    aspect-ratio: 1/1;
    width: 3rem;
    cursor: pointer;

    @include media("<=tablet") {
      top: 3rem;
    }

    @include media("<=phone") {
      top: 4rem;
    }
  }

  & .popup__side {
    position: absolute;
    top: 0;
    left: -2rem;
    width: 10rem;

    @include media("<=phone") {
      display: none;
    }
  }
  & h1 {
    font-size: 5rem;
    font-family: nockenSemiBold;
    @include media("<=phone") {
      font-size: 3rem;
    }
  }

  & .popup__content {
    display: grid;
    grid-template-columns: [col-start-1]2fr[col-end-1 col-start-1] 1fr [col-end-2];
    grid-template-rows: [row-start-1]1fr[row-end-1];
    column-gap: 2rem;
    transition: all 0.3s ease;
    height: 100%;
    width: 100%;
    overflow: scroll;
    padding-bottom: 5rem;

    @include media("<=phone") {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    & img {
      grid-column: col-start-1/col-end-1;
      grid-row: row-start-1/row-end-1;
      aspect-ratio: 1/1;
      object-fit: cover;
      width: 100%;
    }

    &--text {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      text-wrap: wrap;
      font-size: 2rem;

      & ul {
        @include media("<=phone") {
          padding-left: 2rem;
        }
      }

      @include media("<=phone") {
        padding-top: 1rem;
        font-size: 2.25rem;
      }

      @include media("<=tablet") {
        width: 100%;
      }
    }
  }
}

.popup__background {
  position: fixed;
  background-color: rgba($color: #1e1e1e, $alpha: 1);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200vw;
  height: 200vh;
  top: 0;
  left: 0;
  z-index: 1000;
}

.hidden {
  display: none;
  transition: all 0.3s ease;
}

// Animations
.scene_element--fadeinleft {
  animation-name: fadeInLeft;
  animation-delay: 1s;
  animation-duration: 2s;
}

@keyframes fadeInLeft {
  0% {
    transform: translate3d(-100%, 0, 0);
  }

  100% {
    transform: none;
  }
}
