#hero-main {
  position: relative;
  width: 100vw;
  height: 100%;
}

.hero {
  position: relative;
  //   padding-top: 1rem;
  // background-color: orangered;

  @include media("<=phone") {
    height: 100%;
  }

  &__button--unmute {
    position: absolute;
    cursor: pointer;
    font-size: 2rem;
    bottom: 3rem;
    left: 1rem;
    background-color: transparent;
    border: none;
  }
  //   display: grid;
  //   grid-template-rows: [row-start-1]1fr[row-end-1 row-start-2] 1fr [row-end-2];
  //   grid-template-columns: [col-start-1]1fr[col-end-1 col-start-2];
  //   grid-row-gap: 3rem;

  &__container--top {
    position: relative;
    width: 100%;
    height: 50rem;
    background-color: $color-secondary;

    @include media("<=phone") {
      height: 50rem;
    }

    & video {
      width: 100%;
      height: 100%;
      object-fit: cover;

      @include media("<=phone") {
        height: 100%;
        max-width: 100%;
        object-fit: fill;
      }
    }
  }

  &__container-bottom {
    display: flex;
    border-bottom: 1rem solid $color-primary;

    @include media("<=phone") {
      flex-direction: column;
      width: 100%;
      height: 100%;
    }
  }

  &__container__links {
    position: relative;
    width: 33.33%;
    height: fit-content;
    background-color: $color-secondary;
    border-right: 2px solid $color-primary;

    & a {
      color: $color-white;
    }

    @include media("<=phone") {
      border: none;
      width: 100%;
    }

    & h3 {
      text-align: center;
      position: absolute;
      top: 70%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 3rem;
      width: 100%;

      @include media("<=phone") {
        font-size: 5rem;
        color: $color-primary;
      }
    }

    &:hover h3 {
      font-size: 5rem;
      color: $color-primary;
    }

    & img {
      width: 100%;
      object-fit: cover;
      filter: grayscale(1);
      cursor: pointer;
      transition: all 0.3s ease;

      @include media("<=phone") {
        filter: blur(1.5);
      }

      &:hover {
        filter: grayscale(0);
      }
    }
  }
}

.hero__marquee {
  //   position: absolute;
  //   top: 50%;
  background-color: $color-primary;
  padding: 3rem 1rem;
  width: 100%;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: nockenSemiBold;
  font-size: 3rem;
  font-weight: 600;
  color: $color-secondary;
}
