nav {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 3rem;
  background-color: $color-secondary;

  width: calc(100vw - 6rem);
  height: 1rem;
  z-index: 999;

  & .nav__logo-container {
    & img {
      @include media("<=phone") {
        width: 20rem;
      }
    }
  }

  & .nav__menu-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 5rem;

    & img {
      cursor: pointer;
      @include media("<=phone") {
        width: 5rem;
      }
    }
  }

  @include media("<=phone") {
    padding: 1rem;
    height: 6rem;
    width: 100%;
  }

  @include media("<=desktop") {
    width: 100%;
  }
}

.btn-register {
  position: absolute;
  bottom: -5%;
  left: 42%;
  transform: translate(-5%, -50%);
  text-decoration: none;
  border: none;
  background: $color-primary;
  font-family: nockenSemiBold;

  font-size: 2rem;
  padding: 2rem 3rem;
  // margin-right: -70rem;

  @include media("<=phone") {
    padding: 2rem 3rem;
    left: 30%;
  }
}

.nav__lowerBoder {
  position: absolute;
  left: 0;
  bottom: -3rem;
  width: 100%;

  @include media(">=desktop") {
    bottom: -3.1rem;
  }

  @include media("<desktop", ">=tablet") {
    bottom: -2.6rem;
  }

  @include media("<=tablet") {
    width: 100%;
    bottom: -2rem;
  }

  @include media("<=phone") {
    width: 100rem;
    height: 100%;
    bottom: -4rem;
  }
}
