#about {
  position: relative;
  width: 100vw;
  height: 100vh;
  padding-top: 5rem;
}
.panel {
  position: absolute;
}
.about {
  &__first {
    position: relative;
    width: 100%;
    height: 100%;
    // padding-top: 5rem;
    background-color: #eee;

    @include media("<=phone") {
    }

    & img {
      position: absolute;
      top: -25rem;
      width: 100%;
      // height: 100%;
      object-fit: cover;
    }
  }

  &__container {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    // padding-top: 5rem;
    // background-color: blue;
  }

  &__box--orange {
    background-color: orange;
    width: 100%;
    height: 100%;

    & img {
      width: 100%;
      object-fit: cover;
    }
  }
  &__box--green {
    background-color: greenyellow;
    width: 100%;
    height: 100%;
  }
  &__box--blue {
    background-color: cyan;
    width: 100%;
    height: 100%;
  }
}
