.menu__popup {
  position: fixed;
  width: 100vw;
  min-height: 100vh;
  top: 0;
  left: 0;
  z-index: 1003;
  overflow-y: hidden;

  // background-image: url("../assets/Menu.png");
  background-color: $color-primary;

  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;

  @include media("<=phone") {
    position: fixed;
    min-height: 100vh;
  }

  &__nav {
    position: absolute;
    top: 0;
    left: 0;
    padding: 3rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  &__logo {
    @include media("<=phone") {
      width: 20rem;
    }
  }

  &__close {
    aspect-ratio: 1/1;
    width: 5rem;
    cursor: pointer;

    @include media("<=phone") {
      aspect-ratio: 1/1;
      width: 2.5rem;
    }
  }

  &__lowerBorder {
    position: absolute;
    bottom: -1rem;
    left: 0;
    width: 100%;
  }

  &__content {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;

    & a {
      color: $color-secondary;
      font-size: 5rem;
      cursor: pointer;

      @include media("<=tablet") {
        font-size: 3rem;
      }
    }

    &--1 {
      display: flex;
      flex-direction: column;
      gap: 30rem;
      text-align: center;

      @include media("<=phone") {
        display: none;
      }
    }

    &--2 {
      position: absolute;
      display: flex;
      gap: 60rem;
      text-align: center;
      @include media("<=phone") {
        display: none;
      }
    }

    &--3 {
      display: none;

      @include media("<=phone") {
        display: flex;
        flex-direction: column;
        text-align: center;
        gap: 5rem;
      }
    }
  }
}
