/* Handle on hover */

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  @include media("<=tablet") {
    font-size: 56.25%; //1rem = 9px
  }
  @include media(">=desktop") {
    font-size: 75%; //1rem = 12px
  }

  @include media("<=phone") {
    font-size: 50%;
  }
}

body {
  background-color: $color-secondary;
  font-family: nocken;
  color: $color-white;
  scroll-behavior: smooth;
  position: relative;
  & a {
    color: $color-white;
  }
}

