footer {
  width: 100vw;
  height: 50vh;
  color: $color-primary;
  background-color: $color-secondary;
  overflow: scroll;
  scroll-behavior: smooth;
  text-align: center;
  background-color: $color-white;
  padding-top: 15rem;

  @include media("<=phone") {
    height: 25vh;
    padding-top: 10rem;
  }

  & .footer__link__container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 200vw;
    list-style: none;
    font-size: 10rem;

    @include media("<=phone") {
      font-size: 8rem;
    }

    & a {
      text-decoration: none;
      color: $color-secondary;
    }
  }
}
