.preloader {
  width: 100%;
  height: 100%;
  display: none;

  position: fixed;
  z-index: 1004;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10rem;
  overflow: hidden;

  &__left {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    overflow: hidden;
    width: 50%;
    height: 100%;
    background-color: orangered;
    position: relative;

    & img {
      position: absolute;
      right: 0;
      transform: translateX(50%);

      @include media("<=phone") {
        aspect-ratio: 2/1;
        width: 40rem;
      }
    }
  }
  &__right {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    overflow: hidden;
    width: 50%;
    height: 100%;
    background-color: orangered;
    position: relative;

    & img {
      position: absolute;
      left: 0;
      transform: translateX(-50%);

      @include media("<=phone") {
        aspect-ratio: 2/1;
        width: 40rem;
      }
    }
  }
}
